:root {
  --bottom-margin-text-container-elements:2rem;
  --vertical-padding-main-lockup-button:0.8rem;
  --horizontal-padding-main-lockup-button:2.5rem;
  --font-size-secondary-elements:1.1rem;
  --font-size-primary-elements:3rem;
  --font-color-dark-primary:#0D0D0D;
  --font-color-dark-secondary:#262626;
  --font-color-white-primary:#E6E6E6;
}

.top-image-container {
  height: 100vh;
  color: #404040;
  mask-image: linear-gradient(to right, rgba(0,0,0,1) 50%, transparent 100%);

}
.top-image-container video {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
}
.top-image-container img {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;

}

#vid-1 {
  pointer-events: none;
}

.index-text-container {
  position: relative;
  top: 20%;
  left: 3%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 43.5%;
  height: 48%;
  padding-left: 5%;
  padding-right: 5%;
  border-radius: 6rem;
  
}

.index-title {
  font-family: var(--main-font-family);
  font-size: var(--font-size-primary-elements);
  margin-bottom:var(--bottom-margin-text-container-elements);
  color: var(--font-color-dark-primary);
}

.index-description {
  font-family: var(--main-font-family);
  font-size: var(--font-size-secondary-elements);
  margin-bottom:var(--bottom-margin-text-container-elements) ;
  line-height: 200%;
  color: var(--font-color-dark-secondary);
}

.main-lockup-button {
  border-color: transparent;
  background-color: var(--primary-color);
  padding-left: var(--horizontal-padding-main-lockup-button);
  padding-right: var(--horizontal-padding-main-lockup-button);
  padding-top: var(--vertical-padding-main-lockup-button);
  padding-bottom: var(--vertical-padding-main-lockup-button);
  color: var(--font-color-white-primary);
  font-family: sans-serif;
  font-size: var(--font-size-secondary-elements);
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  width: fit-content;
  border-radius: var(--border-radius-button);
  
}
.main-lockup-button:hover {
  background-color:#506BFC ;
  
}

/* Code for main top container ends here */

/* Code for second container starts here */


.next-div img {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  
}
.test-2 {
  height: 100vh;
  width: 100%;
  z-index: 10;
}

@media (max-width:1750px) {
  :root {
      --font-size-secondary-elements:.9rem;
      --font-size-primary-elements:2rem;
  }
  
}
